<template>
    <div class="img_container">
        <div class="img_container-display"
            :class="{ 'img_container-display-loaded': loaded }"
            ref="display">
        </div>
        <img :src="src" :alt="alt"
            class="img_container-img" ref="img">
    </div>
</template>

<script>
export default {
    props: ['src', 'alt'],
    data() {
        return {
            loaded: false,
        }
    },
    mounted() {
        this.$refs.display.style.backgroundImage = 'url(' + this.src + ')'
        this.$refs.img.onload = this.onload
    },
    methods: {
        onload() {
            this.loaded = true
        }
    }
}

</script>